import Vue from 'vue'
import Router from 'vue-router'
import DisCover from '@/components/index' //引入发现首页
import Study from '@/components/study/study' //引入学习首页
import Particulars from '@/components/study_detail/particulars' //引入课程详情页
import Zhibo from '@/components/zhibo/zhibo' //引入课程详情页
import Index_detail from '@/components/index_detail/index_detail' //引入作业详情页


import Mine from '@/components/mine/mine' //引入用户首页



import Write from '@/components/write/write'
import Tag from '@/components/tag/Tag'
import UnfinishedWork from '@/components/unfinishedWork/UnfinishedWork'

import systemMessage from '@/components/systemmessage/systemmsg' //系统消息
import MyPraise from '@/components/mypraise/MyPraise' //我的赞
import Discuss from '@/components/discuss/discuss' //消息--评论
import Message from '@/components/message/message' //消息--评论
import Feedback from '@/components/feedback/feedback'  //反馈


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'DisCover',
      component: DisCover
    },
    {
      path: '/study',
      name: 'Study',
      component: Study
    },
    {
      path: '/study_detail',
      name: 'Particulars',
      component: Particulars
    },
    {
      path: '/index_detail',
      name: 'Index_detail',
      component: Index_detail
    },
    {
      path: '/zhibo',
      name: 'Zhibo',
      component:Zhibo
    },
    {
      path: '/write',
      name: 'Write',
      component: Write
    },
    {
      path: '/write/:id/:name/:tag/:arttype/:artid',
      name: 'WriteArt',
      component: Write
    },
    {
      path: '/tag',
      name: 'Tag',
      component: Tag
    },
    {
      path: '/unfinishedwork',
      name: 'UnfinishedWork',
      component: UnfinishedWork
    },
    {
      path: '/message',
      name: 'Message',
      component: Message
    },
    {
      path: '/mypraise',
      name: 'MyPraise',
      component: MyPraise

    },
    {
      path: '/systemmessage',
      name: 'systemMessage',
      component: systemMessage
    },
    {
      path: '/discuss',
      name: 'Discuss',
      component: Discuss
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    },
    {
      path: '/feedback',
      name: 'Feedback',
      component: Feedback
    }
  ]
})



