<template>
  <div class="write-wrap">
    <!-- 标签 -->
    <div class="tag-box">
      <div class="tag-label" @click="goTag">
        标签
      </div>
      <div class="tag-item-box">
        <div :class="{tagItem: true, canHide: showTag}">
          {{name}}
        </div>
      </div>
      <div class="handle-bt" @click="showHandle"></div>
    </div>
    <!-- 标题输入框 -->
    <div class="input-box">
      <input type="text" :placeholder="title | resetTit" v-model="editTit"/>
    </div>
    <!-- 内容输入框 -->
    <div class="content-box">
      <vmEditor ref="editor" :content='content'/>
    </div>
    <!-- 编辑器操作控件：保存、发布、删除 -->
    <WriteHandle :class="{canHide: showwin}" @click.native="showHandle" @save="saveMe" @publish="publishMe" @delete="deleteMe"/>
    <!-- 占位块 -->
    <div class="block-bottom"></div>
  </div>
</template>

<script>
import WriteHandle from '@/components/write/WriteHandle'
import vmEditor from 'vm-editor'
import qs from 'qs'

export default {
  name: 'Write',
  data () {
    return {
      title: '请输入您的标题',
      editTit: '',
      showwin: true,
      wordData: [],
      id: '',
      name: '',
      tag: '',
      arttype: '',
      artid: '',
      showTag: true,
      artData: [],
      content: '',
      workTrueId: ''
    }
  },
  components: {
    WriteHandle,
    vmEditor
  },
  methods: {
    showHandle () {
      if (this.showwin) {
        this.showwin = false
      } else {
        this.showwin = true
      }
    },
    // 获取历史编辑文章的详情
    getEditedArt (artid) {
      let url = this.$global.ApiObj.getEditedArtDetail
      let that = this
      let token = window.localStorage.getItem('token')
      this.$http.get(url, {
        params: {
          id: artid,
          token: token
        }
      })
      .then(function (response) {
        that.artData = response.data.data
        that.editTit = response.data.data.title
        that.name = response.data.data.tagName
        that.id = response.data.data.tagId
        that.content = response.data.data.content
      })
      .catch(function (response) {
        console.log(response)
      })
    },
    // 获取未完成作业的详情
    getEditedWork (artid) {
      let url = this.$global.ApiObj.getEditedWorkDetail
      let that = this
      let token = window.localStorage.getItem('token')
      let data = {
        courseWorkId: artid,
        token: token
      }
      this.$http.get(url, {
        params: data
      })
      .then(function (response) {
        let writeInfoId = response.data.data.writeInfoId
        console.log('作品id：' + writeInfoId)
        if (writeInfoId !== null) {
          // 作业已存在草稿
          that.editTit = response.data.data.title
        } else {
          // 作业不存在草稿
          that.title = response.data.data.title
        }
        that.artData = response.data.data
        that.name = response.data.data.tagName
        that.id = response.data.data.tagId
        that.content = response.data.data.content
        that.workTrueId = response.data.data.writeInfoId
      })
      .catch(function (response) {
        console.log(response)
      })
    },
    saveMe () {
      let html = this.$refs.editor.uploadHtml()
      this.content = html
      // 保存草稿
      if (this.showTag) {
        alert('请先选择标签')
        return
      }
      this.sendArt(3, this.artid)
    },
    publishMe () {
      let html = this.$refs.editor.uploadHtml()
      this.content = html
      if (this.showTag) {
        alert('请先选择标签')
        return
      }
      if (this.editTit === '') {
        alert('标题为空')
      } else if (html === '') {
        alert('内容为空')
      } else {
        this.sendArt(1, this.artid)
      }
    },
    deleteMe () {
      if (this.arttype === 'work' && this.trueWorkId === '') {
        this.clearWrite()
      } else if (this.arttype === 'art' && this.artid === '') {
        this.clearWrite()
      } else {
        // 删除草稿
        if (this.arttype === 'work') {
          console.log(this.workTrueId)
          this.deletArt(this.workTrueId)
        } else {
          this.deletArt(this.artid)
        }
      }
    },
    goTag () {
      this.$router.push('/tag')
    },
    sendArt (status, artid) {
      let url = this.$global.ApiObj.sendArt
      let that = this
      let token = window.localStorage.getItem('token')

      if (artid === 'art') {
        artid = ''
      }

      let data = ''

      if (this.arttype === 'work') {
        // 提交作业
        if (this.workTrueId !== '') {
          // 提交新作业
          data = qs.stringify({
            writeInfoId: that.workTrueId,
            token: token,
            title: that.editTit,
            content: that.content,
            tagId: that.id,
            status: status,
            courseWorkId: artid
          })
        } else {
          // 提交已有的作业草稿
          data = qs.stringify({
            writeInfoId: '',
            token: token,
            title: that.editTit,
            content: that.content,
            tagId: that.id,
            status: status,
            courseWorkId: artid
          })
        }
      } else {
        // 提交文章
        data = qs.stringify({
          writeInfoId: artid,
          token: token,
          title: that.editTit,
          content: that.content,
          tagId: that.id,
          status: status,
          courseWorkId: ''
        })
      }

      this.$http.post(url, data)
      .then(function (response) {
        that.artid = response.data.data
        // 发布作业之后跳转
        if (that.arttype === 'work' && status === 1) {
          alert('作业已发布，要到该去的地方')
          that.$router.push('/')
        } else if (that.arttype === 'art' && status === 1) {
          alert('文章已发布，要到该去的地方')
          that.$router.push('/')
        }
      })
      .catch(function (response) {
        console.log(response)
      })
    },
    deletArt (artid) {
      let url = this.$global.ApiObj.deleteArt
      let that = this

      let data = {
        writeInfoId: artid
      }

      this.$http.get(url, {
        params: data
      })
      .then(function (response) {
        that.$router.push('/write')
        that.editTit = ''
        that.showwin = true
        that.wordData = []
        that.id = ''
        that.name = ''
        that.tag = ''
        that.arttype = ''
        that.artid = ''
        that.showTag = true
        that.artData = []
        that.content = ''
        that.workTrueId = ''
      })
      .catch(function (response) {
        console.log(response)
      })
    },
    clearWrite () {
      this.editTit = ''
      this.content = ''
    }
  },
  mounted () {
    // 取得标签id、标签名字、标签类别、作品类别、作品id
    let id = this.$route.params.id
    let name = this.$route.params.name
    let tag = this.$route.params.tag
    let arttype = this.$route.params.arttype
    let artid = this.$route.params.artid

    // 从非“作业”标签过来的逻辑
    if (arttype === 'art' && id !== 'id') {
      this.id = id
      this.name = name
      this.tag = tag
      this.arttype = arttype
      this.showTag = false
    }
    // 从未完成作业过来的逻辑
    if (arttype === 'work' && artid !== 'art') {
      this.artid = artid
      this.arttype = arttype
      this.showTag = false
      this.getEditedWork(artid)
    }
    // 从历史编辑过来的逻辑
    if (arttype === 'art' && artid !== 'art') {
      this.artid = artid
      this.arttype = arttype
      this.showTag = false
      this.getEditedArt(artid)
    }
  },
  filters: {
    resetTit: function (st) {
      if (!st) return ''
      st = st.toString()
      return st.substring(0, 8) + '...'
    }
  }
}
</script>

<style scoped>
.write-wrap{
  width: auto;
  height: 100%;
  margin:15px;
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

}
.tag-box{
  width:100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
  border-bottom: solid 1px #efefef;
}
.tag-label{
  width: auto;
  height: auto;
  color: #BCB799;
  margin-right: 10px;
}
.tag-item-box{
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.tagItem{
  width: auto;
  height: auto;
  background-color: #f3f5f9;
  border-radius: 3px;
  color: #666666;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
}
.handle-bt{
  width: 20px;
  height: 20px;
  background-image: url('../../assets/write/handleBt.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
}
.input-box{
  width:100%;
  height: auto;
  display: flex;
  border-bottom: solid 1px #efefef;
}
input{
  width: 100%;
  height: auto;
  background-color:none;
  border: solid 0px #ffffff;
  padding:0px;
  margin: 0px;
  color: #666666;
  font-size: 16px;
  padding-top: 15px;
  padding-bottom: 15px;
  outline:none;
}
.content-box{
  width:100%;
  height: auto;
  padding-top: 15px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}
.block-bottom{
  width: auto;
  height: 60px;
}
.canHide{
  display: none;
}

</style>



