<template lang="html">


  <div class="zhuti">

  <div class="content">
    <!-- banner轮播 -->
    <div class="discover_banner">
      <div class="swiper-container">
        <div class="swiper-wrapper">
            <div class="swiper-slide">
              <a href="#">
                <img src="static/images/banner2.png" alt="">
              </a>
            </div>
            <!-- <div class="swiper-slide">
              <a href="#">
                <img src="/static/images/banner.png" alt="">
              </a>
            </div>
            <div class="swiper-slide">
              <a href="#">
                <img src="/static/images/banner2.png" alt="">
              </a>
            </div> -->
        </div>
        <!-- 如果需要分页器 -->
        <div class="swiper-pagination"></div>
      </div>
    </div>

    <!-- 课程列表 -->
    <div class="study">
      <ul class="study_ul">
      <router-link :to="{path:'/study_detail',query: {id:courseLists[index].id}}" v-for="(item,index) in courseLists">
        <li>
          <div class="study_li_l">
            <div class="study_li_l_t">
              <h3 class="add_jing_title">{{courseLists[index].title}}</h3>
              <p class="study_jj">{{courseLists[index].introduction}}</p>
            </div>
            <div class="study_li_l_b">
              <div class="study_li_l_b_l">
                已购买人数<span class="gmrs_sp">{{courseLists[index].buyNum}}</span>
              </div>
              <div class="study_li_l_b_r gm_price">
                ￥{{courseLists[index].cmoney}}
              </div>
            </div>
          </div>
          <div class="study_li_r">
            <img v-bind:src="courseLists[index].cover" alt="">
            <div class="study_qishu">
              <div class="study_qishu_t">
               {{courseLists[index].stage}}期
              </div>
              <div class="study_qishu_b">
                <img src="/static/images//Label.png" alt="">
              </div>
            </div>
            <div class="shichang">
                {{courseLists[index].startTime}}开课
            </div>
          </div>
        </li>
       </router-link>
      </ul>
    </div>
    <foot-bottom></foot-bottom>

  </div>
   <router-view></router-view> 
</div>

</template>

<script>
import footBottom from '../footer/footer.vue'
export default {
  name: 'Study',
  data(){
    return{
      homeUrl:'api-h5/course/getCourseHome?currentPage=1&pageSize=10',  //请求首页数据的接口地址
      courseLists:[],
      id:[],
      title:[],
    }
  },
  components:{
    footBottom
  },
  created(){
    this.getData();
  },
  methods:{
    getData: function(){
      this.$http.get(this.homeUrl).then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              console.log(data);
              //获取课程列表
              this.courseLists = data;
              console.log(this.courseLists);

          }
      }.bind(this))
    }
  }
}
</script>

<style scoped>
*{
  margin:0;
  padding:0;
  border:0;
  list-style:none;
  outline:none;
}

@media screen and (min-width:640px){
    html{font-size:40px;}
}
@media screen and (min-width:414px) and (max-width:639px){
    html{font-size:25.875px;}
}
@media screen and (min-width:375px) and (max-width:413px){
    html{font-size:23.4375px;}
}
@media screen and (min-width:320px) and (max-width:374px){
    html{font-size:20px;}
}

html,body{
  width: 100%;
  height:100%;
}

body{
  margin:0;
  padding:0;
  border:0;
  font-weight:normal;
  font-size:0.6rem;
  color:#666;
  font-family:"Microsoft Yahei",Arial, Helvetica, sans-serif;
}

a{
  text-decoration: none;
  color:#666;
  font-size:0.6rem;
}

h3{
  font-weight:normal;
}

*{-webkit-tap-highlight-color:rgba(255,0,0,0);}

input,button{
  border:none;
  outline:none;
  background:none;
}

::-webkit-input-placeholder,::-webkit-textarea-placeholder {
  color: #999;
}


/*清楚浮动的css代码*/

.clearfloat:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0
}
.clearfloat {
  zoom: 1
}

.zhuti{
  margin:0 auto;
  max-width:640px;
  overflow:hidden;
  min-height:100%;
  position:relative;
}

.content{
  width: 100%;
  margin-bottom: 2.45rem;
}

/*头部轮播*/

.discover_banner{
  width: 100%;
  height: 6.5rem;
  overflow: hidden;
}
.swiper-container{
  width: 100%;
  height: 100%;
}
.swiper-pagination-bullet {
  width: 0.35rem;
  height: 0.35rem;
  display: inline-block;
  border-radius: 100%;
  background: #fff;
  opacity: .5;
}
.swiper-pagination-bullet-active {
  opacity: 1;
  background: #fff;
}
.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 10px;
  left: 0;
  width: 15.4rem;
  padding-right: 0.6rem;
  text-align: right;
}
.swiper-slide img{
  width: 100%;
  height: 100%;
}


/*底部导航*/
.bottom_nav{
  width: 16rem;
  height: 2.5rem;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  border-top: 0.05rem solid #eaeaea;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}
.bottom_nav_con{
  width: 20%;
  height: 100%;
}
.bottom_nav_con_t{
  width: 100%;
  height: 1.3rem;
  position: relative;
  text-align: center;
}
.bottom_nav_con_t_01{
  width: 100%;
  height: 100%;
}
.bottom_nav_con_t_02{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}
.bottom_nav_con_t_01 img,.bottom_nav_con_t_02 img{
  width: 1.05rem;
  height: 1.05rem;
  margin-top: 0.3rem;
}
.bottom_nav_con_b{
  height: 1.2rem;
  font-size: 0.6rem;
  width: 100%;
  text-align: center;
  line-height: 1.2rem;
  color: #999;
}
.bottom_nav_con_block .bottom_nav_con_t_02{
  display: block;
}
.bottom_nav_con_block .bottom_nav_con_t_01{
  display: none;
}
.bottom_nav_con_block .bottom_nav_con_b{
  color: #333;
}
.red_dot{
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  position: absolute;
  top: 0.4rem;
  left: 1.9rem;
  background-color: #F73049;
}










.study{
  width: 14.8rem;
  margin: 0 auto;
  margin-top: 0.6rem;
}
.study_ul{
  width: 100%;
}
.study_ul li{
  width: 100%;
  padding-bottom: 0.6rem;
  margin-bottom: 0.6rem;
  border-bottom: 0.05rem solid #eaeaea;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.study_li_l{
  width: 9.2rem;
}
.study_li_r{
  width: 5rem;
  height: 6.5rem;
  overflow: hidden;
  position: relative;
}
.study_li_l_t{
  width: 100%;
  height: 5.3rem;
}
.add_jing_title{
  line-height: 1.0rem;
  font-size: 0.8rem;
  color: #333;
}
.study_jj{
  margin-top: 0.3rem;
}
.study_li_l_b{
  width: 100%;
  height: 1.2rem;
  display: flex;
  flex-flow: row wrap;
  color: #999;
  justify-content: space-between;
}
.study_li_l_b_l{}
.gmrs_sp{
  margin-left: 0.3rem;
}
.study_li_l_b_r{
  text-align: right;
}
.gm_price{
  font-size: 0.9rem;
  color: #FFB57E;
}
.mianfei{
  color: #FFB57E;
}
.study_li_r img{
  width: 100%;
  height: 100%;
}
.study_qishu{
  width: 0.8rem;
  position: absolute;
  top: 0;
  right: 0.3rem;
}
.study_qishu_t{
  width: 100%;
  padding: 0.3rem 0;
  text-align: center;
  color: #fff;
  font-size: 0.5rem;
  background-color: rgba(255,181,126,0.7);
}
.study_qishu_b{
  width: 100%;
  line-height: 0;
}
.study_qishu_b img{
  width: 100%;
}
.shichang{
  width: 4.5rem;
  height: 1rem;
  line-height: 1rem;
  position: absolute;
  bottom: 0.3rem;
  left: 0.25rem;
  color: #fff;
  background:-webkit-gradient(linear, 0 0, 0 100%, from(rgba(0,0,0,0)), to(rgba(0,0,0,0.3)));
  filter:Alpha(opacity=50);
  font-size: 0.55rem;
  text-align: center;
}

</style>



