<template>
  <div class="work-wrap">

    <!-- 标签列表 -->
    <div class="work-item"  v-for="data in listData" @click="goWrite(data.id)">
      <div class="course-info">
        <div class="course-ico"></div>
        <div class="course-tit">{{data.courseSubName}}</div>
        <div class="course-date">{{data.publishDate | resetDate}}</div>
      </div>
      <div class="work-tit">
        {{data.title}}
      </div>
      <div class="work-info">
        <div class="work-dot"></div>
        <div class="work-rate">{{data.stage + '期' + data.classNum + '班'}}</div>
        <div class="work-tag work-start" v-if="data.workStatus === 0">未编辑</div>
        <div class="work-tag" v-else>继续编辑</div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'UnfinishedWork',
  data () {
    return {
      listData: []
    }
  },
  methods: {
    getList () {
      let url = this.$global.ApiObj.courseWork
      let that = this
      let token = window.localStorage.getItem('token')
      this.$http.get(url, {
        params: {
          token: token,
          currentPage: 1,
          pageSize: 20
        }
      })
      .then(function (response) {
        that.listData = response.data.data
      })
      .catch(function (response) {
        console.log(response)
      })
    },
    goWrite (artId) {
      this.$router.push('/write/id/name/tag/work/' + artId)
    }
  },
  mounted () {
    this.getList()
  },
  filters: {
    resetDate: function (st) {
      if (!st) return ''
      st = st.toString()
      return st.substring(0, st.length - 5)
    }
  }
}
</script>

<style scoped>
.work-wrap{
  width: auto;
  height: 100%;
  background-color: #F3F5F9;
}
.work-item{
  width: auto;
  height: auto;
  padding: 15px;
  border-bottom: solid 1px #eaeaea;
  background-color: #ffffff;
}
.course-info{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 12px;
  color: #999999;
}
.course-ico{
  width: 2px;
  height: 14px;
  margin-right: 5px;
  background-color: #BCB799;
}
.course-tit{
  width: auto;
  text-align: left;
  flex-grow: 1;
}
.course-date{
  width: auto;
}
.work-tit{
  width: auto;
  height: auto;
  font-size: 16px;
  color: #333333;
  line-height: 28px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.work-info{
  width: auto;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.work-dot{
  width: 4px;
  height: 4px;
  background-color: #FFB57E;
  border-radius: 100%;
  margin-right: 10px;
}
.work-rate{
  color: #FFB57E;
  font-size: 12px;
  flex-grow: 1;
}
.work-tag{
  width: auto;
  padding:5px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #f3f5f9;
  font-size: 12px;
  color: #999999;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.work-start{
  color: #BCB799;
}
</style>



