<script>
import footBottom from '../footer/footer.vue'
export default {
  name: 'Mine',
  data(){
    return{
      accountId:'',
      birthday:'',
      chineseZodiac:'',
      email:'',
      headImgUrl:'',
      identityNumber:'',
      money:'',
      nickName:'',
      phone:'',
      profession:'',
      realName:'',
      sex:'',         //性别 男1 女 0
    }
  },
  components:{
    footBottom
  },
  created(){
    this.getUserInfo();
    this.getUserConfig();
  },
  methods:{
    //获取用户个人信息 此时应写在全局 提取到公共js
    getUserInfo: function(){
    this.$http.get("api-h5/user/wx/getUserWxInfo",{
      params:{
         token:'3243a798a09523dae2aff9b0715466c3'
      }
    }).then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              // console.log(data);
              this.headImgUrl = data.headImgUrl;
              this.accountId = data.accountId;
          }
      }.bind(this))
    },
  }
}
</script>

<style lang="css"  scoped>
*{
  margin:0;
  padding:0;
  border:0;
  list-style:none;
  outline:none;
}

@media screen and (min-width:640px){
    html{font-size:40px;}
}
@media screen and (min-width:414px) and (max-width:639px){
    html{font-size:25.875px;}
}
@media screen and (min-width:375px) and (max-width:413px){
    html{font-size:23.4375px;}
}
@media screen and (min-width:320px) and (max-width:374px){
    html{font-size:20px;}
}

html,body{
  width: 100%;
  height:100%;
}

body{
  margin:0;
  padding:0;
  border:0;
  font-weight:normal;
  font-size:0.6rem;
  color:#666;
  font-family:"Microsoft Yahei",Arial, Helvetica, sans-serif;
}

a{
  text-decoration: none;
  color:#666;
  font-size:0.6rem;
}

h3{
  font-weight:normal;
}

*{-webkit-tap-highlight-color:rgba(255,0,0,0);}

input,button{
  border:none;
  outline:none;
  background:none;
}

::-webkit-input-placeholder,::-webkit-textarea-placeholder {
  color: #999;
}


/*清楚浮动的css代码*/

.clearfloat:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0
}
.clearfloat {
  zoom: 1
}

.zhuti{
  margin:0 auto;
  max-width:640px;
  overflow:hidden;
  min-height:100%;
  position:relative;
}

.content{
  width: 100%;
  margin-bottom: 2.45rem;
}

/*头部轮播*/

.discover_banner{
  width: 100%;
  height: 6.5rem;
  overflow: hidden;
}
.swiper-container{
  width: 100%;
  height: 100%;
}
.swiper-pagination-bullet {
  width: 0.35rem;
  height: 0.35rem;
  display: inline-block;
  border-radius: 100%;
  background: #fff;
  opacity: .5;
}
.swiper-pagination-bullet-active {
  opacity: 1;
  background: #fff;
}
.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 10px;
  left: 0;
  width: 15.4rem;
  padding-right: 0.6rem;
  text-align: right;
}
.swiper-slide img{
  width: 100%;
  height: 100%;
}


/*底部导航*/
.bottom_nav{
  width: 16rem;
  height: 2.5rem;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  border-top: 0.05rem solid #eaeaea;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}
.bottom_nav_con{
  width: 20%;
  height: 100%;
}
.bottom_nav_con_t{
  width: 100%;
  height: 1.3rem;
  position: relative;
  text-align: center;
}
.bottom_nav_con_t_01{
  width: 100%;
  height: 100%;
}
.bottom_nav_con_t_02{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}
.bottom_nav_con_t_01 img,.bottom_nav_con_t_02 img{
  width: 1.05rem;
  height: 1.05rem;
  margin-top: 0.3rem;
}
.bottom_nav_con_b{
  height: 1.2rem;
  font-size: 0.6rem;
  width: 100%;
  text-align: center;
  line-height: 1.2rem;
  color: #999;
}
.bottom_nav_con_block .bottom_nav_con_t_02{
  display: block;
}
.bottom_nav_con_block .bottom_nav_con_t_01{
  display: none;
}
.bottom_nav_con_block .bottom_nav_con_b{
  color: #333;
}
.red_dot{
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  position: absolute;
  top: 0.4rem;
  left: 1.9rem;
  background-color: #F73049;
}






.msg_zhuti{
