}
.msg_system_r_r_r{
  display: flex;
}
.msg_system_r_r_r img{
  width: 0.5rem;
  align-self: center;
}
.wddz{
  margin-top: 0;
}
.wddz .msg_system_r{
  border: none;
}

.pinglun .msg_system_r{
  border: none;
}
.pl_img{
  width: 0.7rem;
}

</style>



