<template>
  <div class="btWrap">
    <div class="btItem" @click="publishArticle">发布</div>
    <div class="btItem" @click="saveArticle">保存草稿</div>
    <div class="btItem" @click="deleteArticle">删除</div>
  </div>
</template>

<script>
export default {
  name: 'WriteHandle',
  data () {
    return {
      title: '请输入您的标题'
    }
  },
  methods: {
    // 保存、发布、删除接口
    saveArticle () {
      this.$emit('save')
    },
    publishArticle () {
      this.$emit('publish')
    },
    deleteArticle () {
      this.$emit('delete')
    }

  }
}
</script>

<style scoped>
.btWrap{
  width: auto;
  height: auto;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 5px #ccc;
  border-radius: 3px;
  position: absolute;
  top: 50px;
  right: 15px;
}
.btItem{
  width: 80px;
  height: auto;
  font-size: 16px;
  color: #333333;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: solid 1px #EAEAEA;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 20px;
  padding-right: 20px;
}
.canHide{
  display: none;
}
</style>



