<script>

export default {
  name: 'Zhibo',
  data(){
    return{
      resourceCover:'',
      resourcePath:'',
      description:'',
      testLists:[]
    }
  },
  created(){
    this.courseSubId = this.$route.query.courseSubId;
    this.getData();
  },
  methods:{
    getData: function(){
      this.$http.get("api-h5/course/getCourseSubInfo?courseSubId=3").then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              console.log(data);
              this.resourceCover = data.resourceCover;
              this.resourcePath = "api-h5/course/resource/"+localStorage.getItem('token')+"/"+this.courseSubId+"/"+data.resourcePath+"/";
              this.description = data.description;
              this.testLists = data.testLists;
          }
      }.bind(this));
    }, 
  }
}

</script>

