<template lang="html">
  <ul class="system_msg_ul">
    <li>
      <div class="system_msg_li_l">
        <img src="static/images/mine.png" alt="">
      </div>
      <div class="system_msg_li_r">
        <div class="msg_title">
            {{title}}
        </div>
        <div class="kk_time">
          <div class="kk_time_l">
            {{timeTT}}
          </div>
          <div class="kk_time_r">
            <router-link to="/study">查看详情</router-link>
          </div>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'teacher',
  props: ['title','timeTT']
}
</script>

<style lang="css">
</style>



