<script>

export default {
  name: 'Index_detail',
  data(){
    return{
      comment_input: "hide_comment" ,
      comment_content:'',
      //初始化url地址里传递过来的参数
       writeInfoId:'',

      //初始化作业或者课程详情数据
     
      title:'',
      content:'',
      creatTime:'',
      headImgUrl:'',
      nickName:'',
      praiseCount:'',
      readCount:'',


      //初始化教师评论数据
      teacher_nickName:'',
      teacher_content:'',
      teacher_headImgUrl:'',
      teacher_createdTime:'',
      teacher_praiseCount:'',

      //初始化用户评论数据
      userComments:[],

    }
  },
  created(){
    this.writeInfoId = this.$route.query.writeInfoId;
    this.accountId = this.$route.query.accountId;
    this.getData();
    this.getTeacherComments();
    this.getUserComments();
    this.addComments();
    // alert(this.accountId);
  },
  methods:{
    //获取课程或者作业的详情数据
    getData: function(){
      this.$http.get("api-h5/write/info/getWriteInfo?writeInfoId="+this.writeInfoId).then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              // console.log(data);
              this.title = data.title;
              this.content = data.content;
              this.creatTime = data.creatTime;
              this.headImgUrl = data.headImgUrl;
              this.nickName = data.nickName;
              this.praiseCount = data.praiseCount;
              this.readCount = data.readCount;

          }
      }.bind(this))
    },
    //获取教师点评数据
    getTeacherComments: function(){
      this.$http.get("api-h5/write/comment/getTeacherComment?writeInfoId="+this.writeInfoId).then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              // console.log(data);
              
              this.teacher_nickName = data[0].nickName;
              this.teacher_content = data[0].content;
              this.teacher_headImgUrl = data[0].headImgUrl;
              this.teacher_createdTime = data[0].createdTime;
              this.teacher_praiseCount = data[0].praiseCount;
          }
      }.bind(this))
    },
    //获取普通用户评论数据列表
    getUserComments: function(){
      this.$http.get("api-h5/write/comment/getUserComment?page=1&pageSize=20&writeInfoId="+this.writeInfoId).then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
             
              this.userComments = data;
              console.log(data);
          }
      }.bind(this))
    },
    //添加评论
    addComments: function(){
      this.$http.get("api-h5/write/comment/getUserComment?page=1&pageSize=10&writeInfoId="+this.writeInfoId).then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
             
              this.userComments = data;
              console.log(data);
          }
      }.bind(this))
    },
    //控制评论框的显示与隐藏
    show_comment: function(){
      this.comment_input = "show_comment";
    },
    // 控制评论框的显示与隐藏
    hide_comment: function(){
      this.comment_input = "hide_comment";
    },
    submit_comment: function(){
      this.$http.post("api-h5/write/comment/addUserComment?token=dd118084427e0adafcb469d21bcc2daa&accountId=14&content="+this.comment_content+"&writeInfoId="+this.writeInfoId).then(function(response){
          if(response.data.success){
              console.log(response.data);
              alert('评论成功');
              this.comment_input = "hide_comment";
              this.getUserComments();
          }else{

          }
      }.bind(this))
    }
  }
}

</script>

