<template lang="html">
  <!-- 底部导航 -->
  <div class="bottom_nav clearfloat">

    <div class="bottom_nav_con" v-for="(item,index) in items" v-on:click="addClassFun(index)" v-bind:class="{bottom_nav_con_block:index==qwerqwre}">
      <router-link :to="{path:item.url}" tag="li"  active-class="bottom_nav_con_block">
        <div class="bottom_nav_con_t">
          <div class="bottom_nav_con_t_01">
            <img :src="item.imgsrc1" alt="">
          </div>
          <div class="bottom_nav_con_t_02">
            <img :src="item.imgsrc2" alt="">
          </div>
        </div>
        <div class="bottom_nav_con_b">
          {{item.content}}
        </div>
      </router-link>
    </div>

  </div>
</template>

<script>
//import'../../../static/js/jquery.min.js'
export default {
  name: 'Footer',
  data(){
    return{
        items:[
          {
            imgsrc1:'static/images/discover01.png',
            imgsrc2:'static/images/discover02.png',
            content:'发现',
            url:'/'
          },
          {
            imgsrc1:'static/images/xuexi01.png',
            imgsrc2:'static/images/xuexi02.png',
            content:'学习',
            url:'/study'
          },
          {
            imgsrc1:'static/images/xiezuo01.png',
            imgsrc2:'static/images/xiezuo02.png',
            content:'写作',
            url:'/write'
          },
          {
            imgsrc1:'static/images/xiaoxi01.png',
            imgsrc2:'static/images/xiaoxi02.png',
            content:'消息',
            url:'/message'
          },
          {
            imgsrc1:'static/images/wode01.png',
            imgsrc2:'static/images/wode02.png',
            content:'我的',
            url:'/mine'
          }
        ],
        qwerqwre:"0"
    }
  },
  methods: {
    addClassFun: function(index) {
      this.qwerqwre = index;
    }
  }
}




</script>

<style lang="css">
</style>



