<template lang="html">
  <div class="zhuti msg_zhuti">

    <div class="content">

    <!-- 系统消息 -->
    <div class="msg_system">
      <div class="msg_system_l">
        <img src="static/images/system.png" alt="">
      </div>
      <router-link to='/systemmessage'>
        <div class="msg_system_r">
          <div class="msg_system_r_l">系统消息</div>
          <div class="msg_system_r_r">
            <div class="msg_system_r_r_l">99+</div>
            <div class="msg_system_r_r_r">
              <img src="static/images/ghlu.png" alt="">
            </div>
          </div>
        </div>
      </router-link>
    </div>
    <!-- 我的赞 -->
    <div class="msg_system wddz">
      <div class="msg_system_l">
        <img src="static/images/like.png" alt="">
      </div>
      <router-link to='/mypraise'>
        <div class="msg_system_r">
          <div class="msg_system_r_l">我的赞</div>
          <div class="msg_system_r_r">
            <div class="msg_system_r_r_l"></div>
            <div class="msg_system_r_r_r">
              <img src="static/images/ghlu.png" alt="">
            </div>
          </div>
        </div>
      </router-link>
    </div>
    <!-- 我的评论 -->
    <div class="msg_system pinglun">
      <div class="msg_system_l">
        <img class="pl_img" src="static/images/comment.png" alt="">
      </div>
      <router-link to='/discuss'>
        <div class="msg_system_r">
          <div class="msg_system_r_l">评论</div>
          <div class="msg_system_r_r">
            <div class="msg_system_r_r_l"></div>
            <div class="msg_system_r_r_r">
              <img src="static/images/ghlu.png" alt="">
            </div>
          </div>
        </div>
      </router-link>
    </div>

      <!-- 底部导航 -->
      <foot-bottom></foot-bottom>

    </div>
  </div>
</template>

<script>
import footBottom from '../footer/footer.vue'
export default {
  name: 'Message',
  components:{
    footBottom
  }
}
</script>

<style lang="css" >

*{
	margin:0;
	padding:0;
	border:0;
	list-style:none;
	outline:none;
}

@media screen and (min-width:640px){
    html{font-size:40px;}
}
@media screen and (min-width:414px) and (max-width:639px){
    html{font-size:25.875px;}
}
@media screen and (min-width:375px) and (max-width:413px){
    html{font-size:23.4375px;}
}
@media screen and (min-width:320px) and (max-width:374px){
    html{font-size:20px;}
}

html,body{
  width: 100%;
	height:100%;
}

body{
	margin:0;
	padding:0;
	border:0;
	font-weight:normal;
	font-size:0.6rem;
	color:#666;
	font-family:"Microsoft Yahei",Arial, Helvetica, sans-serif;
}

a{
	text-decoration: none;
	color:#666;
	font-size:0.6rem;
}

h3{
	font-weight:normal;
}

*{-webkit-tap-highlight-color:rgba(255,0,0,0);}

input,button{
	border:none;
	outline:none;
	background:none;
}

::-webkit-input-placeholder,::-webkit-textarea-placeholder {
  color: #999;
}


/*清楚浮动的css代码*/

.clearfloat:after {
	display: block;
	clear: both;
	content: "";
	visibility: hidden;
	height: 0
}
.clearfloat {
	zoom: 1
}
#app{
	position: relative;
	width: 100%;
	height: 100%;
}
.zhuti{
	margin:0 auto;
	max-width:640px;
	overflow:hidden;
	min-height:100%;
	position:relative;
}

.content{
  width: 100%;
  margin-bottom: 2.45rem;
}

/*头部轮播*/

.discover_banner{
  width: 100%;
  height: 6.5rem;
  overflow: hidden;
}
.swiper-container{
  width: 100%;
  height: 100%;
}
.swiper-pagination-bullet {
  width: 0.35rem;
  height: 0.35rem;
  display: inline-block;
  border-radius: 100%;
  background: #fff;
  opacity: .5;
}
.swiper-pagination-bullet-active {
  opacity: 1;
  background: #fff;
}
.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 10px;
  left: 0;
  width: 15.4rem;
  padding-right: 0.6rem;
  text-align: right;
}
.swiper-slide img{
  width: 100%;
  height: 100%;
}

/*悬浮回首页*/
.xuanfu{
  width: 3.2rem;
  height: 1.2rem;
  position: fixed;
  top: 2rem;
  z-index: 9;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  border-bottom-right-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  background-color: rgba(0,0,0,0.4);
}
.xuanfu_l{
  display: flex;
  margin-left: 0.5rem;
}
.xuanfu_l img{
  align-self: center;
  width: 0.8rem;
}
.xuanfu_r{
  color: #fff;
  line-height: 1.2rem;
  margin-left: 0.3rem;
}

/*自定义插图*/

.zdyct img{
	max-width: 100%;
}


/*底部导航*/
.bottom_nav{
  width: 16rem;
  height: 2.5rem;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  border-top: 0.05rem solid #eaeaea;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
}
.bottom_nav_con{
  width: 20%;
  height: 100%;
}
.bottom_nav_con_t{
  width: 100%;
  height: 1.3rem;
  position: relative;
  text-align: center;
}
.bottom_nav_con_t_01{
  width: 100%;
  height: 100%;
}
.bottom_nav_con_t_02{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}
.bottom_nav_con_t_01 img,.bottom_nav_con_t_02 img{
  width: 1.05rem;
  height: 1.05rem;
  margin-top: 0.3rem;
}
.bottom_nav_con_b{
  height: 1.2rem;
  font-size: 0.6rem;
  width: 100%;
  text-align: center;
  line-height: 1.2rem;
  color: #999;
}
.bottom_nav_con_block .bottom_nav_con_t_02{
  display: block;
}
.bottom_nav_con_block .bottom_nav_con_t_01{
  display: none;
}
.bottom_nav_con_block .bottom_nav_con_b{
  color: #333;
}
.red_dot{
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  position: absolute;
  top: 0.4rem;
  left: 1.9rem;
  background-color: #F73049;
}


.msg_zhuti{
  background-color: #F3F5F9;
}
.msg_system{
  width: 100%;
  height: 2.25rem;
  margin-top: 0.6rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  background-color: #fff;
}
.msg_system_l{
  width:2.25rem;
  align-self: center;
  text-align: center;
}
.msg_system_l img{
  width: 0.8rem;
}
.msg_system_r{
  width: 13.75rem;
  height: 2.2rem;
  line-height: 2.2rem;
  display: flex;
  flex-flow: row wrap;
  font-size: 0.7rem;
  justify-content: space-between;
  border-bottom: 0.05rem solid #eaeaea;
}
.msg_system_r_r{
  margin-right: 0.6rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.msg_system_r_r_l{
  color: #FFB57E;
  margin-right: 0.3rem;
}
.msg_system_r_r_r{
  display: flex;
}
.msg_system_r_r_r img{
  width: 0.5rem;
  align-self: center;
}
.wddz{
  margin-top: 0;
}
.wddz .msg_system_r{
  border: none;
}

.pinglun .msg_system_r{
  border: none;
}
.pl_img{
  width: 0.7rem;
}

</style>



