<template lang="html">
  <div class="zhuti">

    <div class="content">

    <!-- 系统消息 -->

    <div class="system_msg">

      <!-- 开课提醒 -->
      <ul class="system_msg_ul">
        <li>
          <div class="system_msg_li_l">
            <img src="=static/images/mine.png" alt="">
          </div>
          <div class="system_msg_li_r">
            <div class="msg_title">
              你评论了<span>《人人都需要的阅读与写作》</span>
            </div>
            <div class="xxkc">我可以人忍耐你的写作，但是我不知道你写的是什么，为什么什么都没有，还能加精</div>
            <div class="kk_time">
              <div class="kk_time_l">
                10-23  <span>18:13</span>
              </div>
              <div class="kk_time_r">
                查看详情
              </div>
            </div>
          </div>
        </li>
      </ul>

    </div>


  </div>


  </div>
</template>

<script>
export default {
  name: 'Discuss'
}
</script>


<style lang="css" >

*{
	margin:0;
	padding:0;
	border:0;
	list-style:none;
	outline:none;
}

@media screen and (min-width:640px){
    html{font-size:40px;}
}
@media screen and (min-width:414px) and (max-width:639px){
    html{font-size:25.875px;}
}
@media screen and (min-width:375px) and (max-width:413px){
    html{font-size:23.4375px;}
}
@media screen and (min-width:320px) and (max-width:374px){
    html{font-size:20px;}
}

html,body{
  width: 100%;
	height:100%;
}

body{
	margin:0;
	padding:0;
	border:0;
	font-weight:normal;
	font-size:0.6rem;
	color:#666;
	font-family:"Microsoft Yahei",Arial, Helvetica, sans-serif;
}

a{
	text-decoration: none;
	color:#666;
	font-size:0.6rem;
}

h3{
	font-weight:normal;
}

*{-webkit-tap-highlight-color:rgba(255,0,0,0);}

input,button{
	border:none;
	outline:none;
	background:none;
}

::-webkit-input-placeholder,::-webkit-textarea-placeholder {
  color: #999;
}


/*清楚浮动的css代码*/

.clearfloat:after {
	display: block;
	clear: both;
	content: "";
	visibility: hidden;
	height: 0
}
.clearfloat {
	zoom: 1
}
#app{
	position: relative;
	width: 100%;
	height: 100%;
}
.zhuti{
	margin:0 auto;
	max-width:640px;
	overflow:hidden;
	min-height:100%;
	position:relative;
}

.content{
  width: 100%;
  margin-bottom: 2.45rem;
}

/*头部轮播*/

.discover_banner{
  width: 100%;
  height: 6.5rem;
  overflow: hidden;
}
.swiper-container{
  width: 100%;
  height: 100%;
}
.swiper-pagination-bullet {
  width: 0.35rem;
  height: 0.35rem;
  display: inline-block;
  border-radius: 100%;
  background: #fff;
  opacity: .5;
}
.swiper-pagination-bullet-active {
  opacity: 1;
  background: #fff;
}
.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 10px;
  left: 0;
  width: 15.4rem;
  padding-right: 0.6rem;
  text-align: right;
}
.swiper-slide img{
  width: 100%;
  height: 100%;
}

/*悬浮回首页*/
.xuanfu{
  width: 3.2rem;
  height: 1.2rem;
  position: fixed;
  top: 2rem;
  z-index: 9;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  border-bottom-right-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  background-color: rgba(0,0,0,0.4);
}
.xuanfu_l{
  display: flex;
  margin-left: 0.5rem;
}
.xuanfu_l img{
  align-self: center;
  width: 0.8rem;
}
.xuanfu_r{
  color: #fff;
  line-height: 1.2rem;
  margin-left: 0.3rem;
}

/*自定义插图*/

.zdyct img{
	max-width: 100%;
}


/*底部导航*/
.bottom_nav{
  width: 16rem;
  height: 2.5rem;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  border-top: 0.05rem solid #eaeaea;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
}
.bottom_nav_con{
  width: 20%;
  height: 100%;
}
.bottom_nav_con_t{
  width: 100%;
  height: 1.3rem;
  position: relative;
  text-align: center;
}
.bottom_nav_con_t_01{
  width: 100%;
  height: 100%;
}
.bottom_nav_con_t_02{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}
.bottom_nav_con_t_01 img,.bottom_nav_con_t_02 img{
  width: 1.05rem;
  height: 1.05rem;
  margin-top: 0.3rem;
}
.bottom_nav_con_b{
  height: 1.2rem;
  font-size: 0.6rem;
  width: 100%;
  text-align: center;
  line-height: 1.2rem;
  color: #999;
}
.bottom_nav_con_block .bottom_nav_con_t_02{
  display: block;
}
.bottom_nav_con_block .bottom_nav_con_t_01{
  display: none;
}
.bottom_nav_con_block .bottom_nav_con_b{
  color: #333;
}
.red_dot{
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  position: absolute;
  top: 0.4rem;
  left: 1.9rem;
  background-color: #F73049;
}

.system_msg{
  width: 14.8rem;
  padding: 0 0.6rem;
  padding-top: 0.85rem;
}
.system_msg_ul{
  width: 100%;
}
.system_msg_ul li{
  width: 100%;
  margin-bottom: 0.85rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.system_msg_li_l{
  width: 1.7rem;
  height: 1.7rem;
  overflow: hidden;
  border-radius: 50%;
}
.system_msg_li_l img{
  width: 100%;
  height: 100%;
}
.system_msg_li_r{
  width: 12.5rem;
  padding-bottom: 0.85rem;
  border-bottom: 0.05rem solid #eaeaea;
}
.msg_title{
  width: 100%;
  font-size: 0.7rem;
  color: #333;
  line-height: 1.2rem;
}
.msg_title span{
  color: #FDA66B;
}
.msg_time{
  margin-top: 0.85rem;
  color: #999;
}
.msg_time span{
  margin-left: 0.3rem;
}
.kktx_by{
  width: 100%;
  color: #999;
  margin-bottom: 0.6rem;
}
.xxkc{
  color: #999;
  line-height: 1.2rem;
}
.xxkc{
  margin-top: 0.85rem;
}
.kk_time{
  width: 100%;
  margin-top: 0.85rem;
  color: #999;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.kk_time_r{
  color: #BCB799;
}
.kk_time_r a{
  color: #BCB799;
}


</style>



