<template lang="html">
  <div class="zhuti">

    <div class="content">

      <!-- 反馈区域 -->
      <div class="feedback_area">
        <textarea v-model="content" name="name" class="feedback_text" placeholder="请将你的问题，尽可能简洁的发送给，工作人员收集后，将会第一时间处理你所提出的问题(10-400)" ></textarea>
      </div>

      <!-- 联系方式 -->
      <div class="lxfs">
        <div class="lxfs_l">联系方式</div>
        <div class="lxfs_r">
          <input v-model="contact" class="lxfs_r_input" type="text" name="" value="" placeholder="电话/邮箱/QQ/其他">
        </div>
      </div>

      <!-- 提交 -->
      <div class="tjfk" @click="getData">提交</div>

    </div>

  </div>
</template>

<script>
import qs from 'qs'
export default {
  name: 'Feedback',
  data(){
    return{
      homeUrl:this.$global.ApiObj.Feedback,  //请求数据的接口地址
      content: '',
      contact:''
    }
  },
  methods:{
    getData: function(){
      if(this.content==''){
        alert('内容不能为空')
        return
      }
      let that = this
      let data = qs.stringify({
            token:  window.localStorage.getItem('token'),
            terminalType: 1,
            remark: this.contact,
            content: this.content
          })
      this.$http.post(this.homeUrl,data)
      .then(function (response) {
        that.msgList = response.data.data
        console.log(response)
      })
      .catch(function (response) {
        console.log(response)
      })
    }
  }
}
</script>

<style lang="css">

*{
	margin:0;
	padding:0;
	border:0;
	list-style:none;
	outline:none;
}

@media screen and (min-width:640px){
    html{font-size:40px;}
}
@media screen and (min-width:414px) and (max-width:639px){
    html{font-size:25.875px;}
}
@media screen and (min-width:375px) and (max-width:413px){
    html{font-size:23.4375px;}
}
@media screen and (min-width:320px) and (max-width:374px){
    html{font-size:20px;}
}

html,body{
  width: 100%;
	height:100%;
}

body{
	margin:0;
	padding:0;
	border:0;
	font-weight:normal;
	font-size:0.6rem;
	color:#666;
	font-family:"Microsoft Yahei",Arial, Helvetica, sans-serif;
}

a{
	text-decoration: none;
	color:#666;
	font-size:0.6rem;
}

h3{
	font-weight:normal;
}

*{-webkit-tap-highlight-color:rgba(255,0,0,0);}

input,button{
	border:none;
	outline:none;
	background:none;
}

::-webkit-input-placeholder,::-webkit-textarea-placeholder {
  color: #999;
}


/*清楚浮动的css代码*/

.clearfloat:after {
	display: block;
	clear: both;
	content: "";
	visibility: hidden;
	height: 0
}
.clearfloat {
	zoom: 1
}
#app{
	position: relative;
	width: 100%;
	height: 100%;
}
.zhuti{
	margin:0 auto;
	max-width:640px;
	overflow:hidden;
	min-height:100%;
	position:relative;
}

.content{
  width: 100%;
  margin-bottom: 2.45rem;
}

/*头部轮播*/

.discover_banner{
  width: 100%;
  height: 6.5rem;
  overflow: hidden;
}
.swiper-container{
  width: 100%;
  height: 100%;
}
.swiper-pagination-bullet {
  width: 0.35rem;
  height: 0.35rem;
  display: inline-block;
  border-radius: 100%;
  background: #fff;
  opacity: .5;
}
.swiper-pagination-bullet-active {
  opacity: 1;
  background: #fff;
}
.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 10px;
  left: 0;
  width: 15.4rem;
  padding-right: 0.6rem;
  text-align: right;
}
.swiper-slide img{
  width: 100%;
  height: 100%;
}

/*悬浮回首页*/
.xuanfu{
  width: 3.2rem;
  height: 1.2rem;
  position: fixed;
  top: 2rem;
  z-index: 9;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  border-bottom-right-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  background-color: rgba(0,0,0,0.4);
}
.xuanfu_l{
  display: flex;
  margin-left: 0.5rem;
}
.xuanfu_l img{
  align-self: center;
  width: 0.8rem;
}
.xuanfu_r{
  color: #fff;
  line-height: 1.2rem;
  margin-left: 0.3rem;
}

/*自定义插图*/

.zdyct img{
	max-width: 100%;
}


/*底部导航*/
.bottom_nav{
  width: 16rem;
  height: 2.5rem;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  border-top: 0.05rem solid #eaeaea;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
}
.bottom_nav_con{
  width: 20%;
  height: 100%;
}
.bottom_nav_con_t{
  width: 100%;
  height: 1.3rem;
  position: relative;
  text-align: center;
}
.bottom_nav_con_t_01{
  width: 100%;
  height: 100%;
}
.bottom_nav_con_t_02{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}
.bottom_nav_con_t_01 img,.bottom_nav_con_t_02 img{
  width: 1.05rem;
  height: 1.05rem;
  margin-top: 0.3rem;
}
.bottom_nav_con_b{
  height: 1.2rem;
  font-size: 0.6rem;
  width: 100%;
  text-align: center;
  line-height: 1.2rem;
  color: #999;
}
.bottom_nav_con_block .bottom_nav_con_t_02{
  display: block;
}
.bottom_nav_con_block .bottom_nav_con_t_01{
  display: none;
}
.bottom_nav_con_block .bottom_nav_con_b{
  color: #333;
}
.red_dot{
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  position: absolute;
  top: 0.4rem;
  left: 1.9rem;
  background-color: #F73049;
}

.zhuti{
  background-color: #F3F5F9;
}
.feedback_area{
  width: 14.8rem;
  padding: 0.6rem;
  margin-top: 0.85rem;
  background-color: #fff;
  height: 6rem;
}
.feedback_text{
  width: 100%;
  height: 100%;
  resize: none;
  line-height: 1.1rem;
  color: #666;
}
.lxfs{
  width: 14.8rem;
  height: 2rem;
  background-color: #fff;
  margin-top: 0.85rem;
  padding: 0 0.6rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.lxfs_l{
  height: 100%;
  line-height: 2rem;
  color: #333;
  font-size: 0.7rem;
}
.lxfs_r{
  margin-left: 0.6rem;
  height: 100%;
  width: 11rem;
}
.lxfs_r_input{
  width: 100%;
  height: 100%;
  letter-spacing: 0.1rem;
}
.tjfk{
  width: 14.8rem;
  height: 1.8rem;
  margin: 0.85rem auto;
  text-align: center;
  line-height: 1.8rem;
  background-color: #FFB57E;
  border-radius: 0.2rem;
  font-size: 0.7rem;
  color: #fff;
}

/*弹出组件*/
.zhezhao{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4);
  position: fixed;
  top: 0;
  z-index: 11;
  /*display: none;*/
}
.xgnc{
  width: 12rem;
  height: 10rem;
  background-color: #fff;
  z-index: 11;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -5rem;
  margin-left: -6rem;
  border-radius: 0.2rem;
  /*display: none;*/
}
.xgnc_t{
  width: 100%;
  height: 1.95rem;
  text-align: center;
  line-height: 2rem;
  color: #333;
  font-size: 0.7rem;
  border-bottom: 0.05rem solid #eaeaea;
}
.xgnc_c {
     width: auto;
    height: 4.8rem;
    font-size: 0.7rem;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    color: #333;
    padding: 0.6rem;
    line-height: 1.1rem;
}
.xgnc_b{
  width: 100%;
  height: 1.95rem;
  border-top: 0.05rem solid #eaeaea;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  text-align: center;
  line-height: 1.95rem;
}
.xgnc_b_l{
  width: 6rem;
  height: 100%;
}
.xgnc_b_r{
  width: 5.95rem;
  height: 100%;
  color: #BCB799;
  border-left: 0.05rem solid #eaeaea;
}

/*下滑动画*/

.animated {

	-webkit-animation-duration:1s;

	animation-duration:1s;

	-webkit-animation-fill-mode:both;

	animation-fill-mode:both

}

.animated.infinite {

	-webkit-animation-iteration-count:infinite;

	animation-iteration-count:infinite

}

.animated.hinge {

	-webkit-animation-duration:2s;

	animation-duration:2s

}



@-webkit-keyframes bounceInDown {

	0% {

	opacity:0;

	-webkit-transform:translateY(-2000px);

	transform:translateY(-2000px)

}

60% {

	opacity:1;

	-webkit-transform:translateY(30px);

	transform:translateY(30px)

}

80% {

	-webkit-transform:translateY(-10px);

	transform:translateY(-10px)

}

100% {

	-webkit-transform:translateY(0);

	transform:translateY(0)

}

}@keyframes bounceInDown {

	0% {

	opacity:0;

	-webkit-transform:translateY(-2000px);

	-ms-transform:translateY(-2000px);

	transform:translateY(-2000px)

}

60% {

	opacity:1;

	-webkit-transform:translateY(30px);

	-ms-transform:translateY(30px);

	transform:translateY(30px)

}

80% {

	-webkit-transform:translateY(-10px);

	-ms-transform:translateY(-10px);

	transform:translateY(-10px)

}

100% {

	-webkit-transform:translateY(0);

	-ms-transform:translateY(0);

	transform:translateY(0)

}

}

.bounceInDown {

	-webkit-animation-name:bounceInDown;

	animation-name:bounceInDown

}



@-webkit-keyframes slideInRight {

	0% {

	opacity:0;

	-webkit-transform:translateX(2000px);

	transform:translateX(2000px)

}

100% {

	-webkit-transform:translateX(0);

	transform:translateX(0)

}

}@keyframes slideInRight {

	0% {

	opacity:0;

	-webkit-transform:translateX(2000px);

	-ms-transform:translateX(2000px);

	transform:translateX(2000px)

}

100% {

	-webkit-transform:translateX(0);

	-ms-transform:translateX(0);

	transform:translateX(0)

}

}.slideInRight {

	-webkit-animation-name:slideInRight;

	animation-name:slideInRight

}


</style>



