<script>
import footBottom from './footer/footer.vue'
export default {
  name: 'Index',
  data(){
    return{
      workLists:[],
    }
  },
  components:{
    footBottom
  },
  created(){
    this.getData();
  },
  methods:{
    getData: function(){
      this.$http.get("/api-h5/write/info/getPageWriteInfo?page=1&pageSize=10").then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              console.log(data);
              //获取课程列表
              this.workLists = data;
              console.log(this.workLists);

          }
      }.bind(this))
    }
  }
}

