/*清楚浮动的css代码*/

.clearfloat:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0
}
.clearfloat {
  zoom: 1
}

.zhuti{
  margin:0 auto;
  max-width:640px;
