  text-align: center;
}
.bottom_nav_con_t_01{
  width: 100%;
  height: 100%;
}
.bottom_nav_con_t_02{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}
.bottom_nav_con_t_01 img,.bottom_nav_con_t_02 img{
  width: 1.05rem;
  height: 1.05rem;
  margin-top: 0.3rem;
}
.bottom_nav_con_b{
  height: 1.2rem;
  font-size: 0.6rem;
  width: 100%;
  text-align: center;
  line-height: 1.2rem;
  color: #999;
}
.bottom_nav_con_block .bottom_nav_con_t_02{
