<script>

export default {
  name: 'Particulars',
  data(){
    return{
      //定义刚开始数据不是全部显示的
      src: "static/images/open.png",
      show: false,
      id:'',
      title:'',
      description: '' ,  //下面一段是课程描述
      introduction: '', //上面一段是课程介绍
      startTime: '',
      sonCourseLists:[],

    }
  },
  created(){
    this.id = this.$route.query.id;
    // alert(this.id);
    this.getData();
  },
  methods:{
    showMore: function(){
        this.show = !this.show;
        if(this.show == true){
          this.src = "static/images/stop.png";
        }else{
          this.src = "static/images/open.png";
        }
    },
    //获取课程详情
    getData: function(){
      this.$http.get('api-h5/course/getCourseInfo?courseId='+this.id).then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              console.log(data);
              this.title = data.title;
              this.introduction = data.introduction;
              this.description = data.description;
              this.startTime = data.startTime;

          }
      }.bind(this));
      //获取课程的子课程列表
      this.$http.get('api-h5/course/getCourseSub?courseId='+this.id+'&currentPage=1&pageSize=10').then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              console.log(data);
              this.sonCourseLists = data;

          }
      }.bind(this));
    },
    status: function(){
        var result = true;
        // ...
        return result;
    }
  }
}

</script>

