<template lang="html">
  <ul class="system_msg_ul">
    <li>
      <div class="system_msg_li_l">
        <img src="static/images/mine.png" alt="">
      </div>
      <div class="system_msg_li_r">
        <div class="kktx_by">{{title}}</div>
        <div class="msg_title">
          {{message}}
        </div>
        <div class="xxkc">课程名称：{{name}}</div>
        <div class="xxkc">开课时间：{{timeTT}}</div>
        <div class="kk_time">
          <div class="kk_time_l">
            {{createdTime}}
          </div>
          <div class="kk_time_r">
            <router-link to="/study">查看详情</router-link>
          </div>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'jihua',
  props: ['pushSubType','name','title','timeTT','message','createdTime']
}
</script>

<style lang="css">
</style>



