<template>
  <div class="tag-wrap">
    <GoHome/>

    <!-- 标签列表 -->
    <div class="tag-tit">标签选择</div>

    <div class="tag-box">
      <div v-for="data in tagData" class="tag-item" @click="goTagHandle(data.type, data.name, data.id)">
        {{data.name}}
      </div>
    </div>

    <!-- 草稿列表 -->
    <div :class="{'tag-tit': true, canHide: showArt}">历史编辑(最多保留5条历史编辑)</div>

    <div :class="{'tag-box': true, canHide: showArt}">
      <div v-for="data in artData" class="art-item" @click="goArtHandle(data.id)">
        {{data.title | resetTit}}{{data.updatedTime | resetDate}}
      </div>
    </div>

  </div>
</template>

<script>
import GoHome from '@/components/common/GoHome'
export default {
  name: 'Tag',
  data () {
    return {
      showArt: true,
      tagData: [],
      artData: []
    }
  },
  methods: {
    // 获取标签列表
    getTag () {
      let url = this.$global.ApiObj.getTag
      let that = this
      this.$http.get(url, {
      })
      .then(function (response) {
        that.tagData = response.data.data
      })
      .catch(function (response) {
        console.log(response)
      })
    },
    // 获取历史编辑文章
    getEditedArt () {
      let url = this.$global.ApiObj.getEditedArt
      let that = this
      let token = window.localStorage.getItem('token')
      this.$http.get(url, {
        params: {
          token: token
        }
      })
      .then(function (response) {
        that.artData = response.data.data
        if (that.artData.length > 0) {
          that.showArt = false
        }
      })
      .catch(function (response) {
        console.log(response)
      })
    },
    // 点击标签的处理方法
    goTagHandle (tag, tagName, id) {
      if (tag === 1) {
        // 如果标签是作业，就去未完成作业列表
        this.$router.push('/unfinishedwork')
      } else {
        // 如果标签不是作业，就去写作主页写一篇新文章
        this.$router.push('/write/' + id + '/' + tagName + '/' + tag + '/art/art')
      }
    },
    // 点击历史编辑的处理方法
    goArtHandle (artid) {
      // 去写作主页继续编辑改文章
      this.$router.push('/write/id/name/tag/art/' + artid)
    }
  },
  components: {
    GoHome
  },
  mounted () {
    this.getTag()
    this.getEditedArt()
  },
  filters: {
    resetTit: function (st) {
      if (!st) return ''
      st = st.toString()
      if (st.length > 4) {
        return st.substring(0, 4) + '... '
      } else {
        return st + ' '
      }
    },
    resetDate: function (st) {
      if (!st) return ''
      st = st.toString()
      return st.substring(0, st.length - 5)
    }
  }
}
</script>

<style scoped>
.tag-wrap{
  width: auto;
  height: auto;
}
.tag-tit{
  width: auto;
  height: 100%;
  margin:0px;
  padding-left: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 12px;
  color: #999999;
  background-color: #F3F5F9;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.tag-box{
  width: auto;
  height: auto;
  padding-top: 25px;
  padding-bottom: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.tag-item{
  width: auto;
  height: auto;
  background-color: #f3f5f9;
  border-radius: 3px;
  color: #666666;
  font-size: 16px;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 15px;
}
.art-item{
  width: auto;
  height: auto;
  background-color: #f3f5f9;
  border-radius: 3px;
  color: #999999;
  font-size: 12px;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 15px;
}
.canHide{
  display: none;
}
</style>



