<template lang="html">
  <div class="">
    <ul class="system_msg_ul">
      <li>
        <div class="system_msg_li_l">
          <img src="static/images/mine.png" alt="">
        </div>
        <div class="system_msg_li_r">
          <div class="msg_title">
            {{ message }}
          </div>
          <div class="msg_time">
              <!-- 10-23  <span>18:13</span> -->
              {{createdTime}}
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'banned',
  props: ['message','createdTime']
}
</script>

<style lang="css">
</style>



