<template>
  <div class="gohome-wrap" @click="goHome">
    <div class="gohome-ico"></div>
    首页
  </div>
</template>

<script>
export default {
  name: 'GoHome',
  methods: {
    goHome () {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>
.gohome-wrap{
  width: auto;
  height: auto;
  position: fixed;
  top: 25px;
  left: 0px;
  padding-left: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 10px;
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow:  0 0 2px rgba(0, 0, 0, 0.4);
  border-radius: 0 30px 30px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 12px;
}
.gohome-ico{
  width: 16px;
  height: 16px;
  margin-right: 5px;
  background-image: url('../../assets/common/gohome.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
}
</style>



