// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import axios from 'axios'
Vue.prototype.$http = axios

import Global from '@/components/common/Global'
Vue.prototype.$global = Global

//获取用户个人信息配置 定义一个全局的自执行函数 不管从哪个页面进来 都是可以拿到用户的信息
Vue.prototype.getUserConfig = function(){
	this.$http.get("api-h5/user/config/queryUserConfig",{
      params:{
         token:'3243a798a09523dae2aff9b0715466c3'
      }
    }).then(function(response){
          if(response.data.success){
              //请求数据成功
              const data = response.data.data;
              console.log(data);

              const bannedTime = data.bannedTime //禁言开启时间
              localStorage.setItem('bannedTime',bannedTime)

              const bannedDurationM = data.bannedDurationM //禁言持续时间
              localStorage.setItem('bannedDurationM',bannedDurationM)

              const memberTime = data.memberTime //会员开启日期
              localStorage.setItem('memberTime',memberTime)

              const memberDurationD = data.memberDurationD //会员持续时间
              localStorage.setItem('memberDurationD',memberDurationD)

              const bannedLastMinutes = data.bannedLastMinutes //剩余禁言时间
              localStorage.setItem('bannedLastMinutes',bannedLastMinutes)

              const isMember = data.isMember //是否是会员
              localStorage.setItem('isMember',isMember);
          }
      })
}

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  methods: {
    getToken () {
      let url = this.$global.ApiObj.getToken
      this.$http.get(url, {
        params: {
          accountId: '11'
        }
      })
      .then(function (response) {
        window.localStorage.setItem('token', response.data.data)
      })
      .catch(function (response) {
        console.log(response)
      })
    }
  },
  mounted () {
    this.getToken()
  }
})



